"use client";

import React, { useState, useEffect, ReactNode, useCallback } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import {
  YadakchiAdminLayout,
  InfoCard,
  Label,
  ActionToolbar,
  EntityHeader,
  Badge,
  ApprovalCard,
  Button,
  Modal,
} from "@my-ds/design-system";

import { getShopById, confirmShopRegistration, confirmFinalShopStatus, type Shop } from "../../../services/shopsService";
import { translateStatus } from "../../../utils/translations";
import { CheckBadgeIcon } from "@heroicons/react/24/solid";

// پارامترهای ورودی صفحه
interface ShopDetailsPageProps {
  params: {
    id: string;
  }
}

export default function ShopDetailsPage({ params }: ShopDetailsPageProps) {
  const { id } = params;
  const router = useRouter();
  
  // --- State Management ---
  const [shop, setShop] = useState<Shop | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [actionLoading, setActionLoading] = useState(false);
  
  // State مرکزی برای مدیریت مودال
  const [modalState, setModalState] = useState({
    isOpen: false,
    title: '',
    message: '',
    onConfirm: () => {}, // تابعی که با تایید اجرا می‌شود
  });

  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;

  // --- Data Fetching ---
  const fetchShop = useCallback(async () => {
    if (!id) return;
    try {
      setIsLoading(true);
      setError(null);
      const shopData = await getShopById(id);
      setShop(shopData);
    } catch (err) {
      setError(err instanceof Error ? err.message : "یک خطای ناشناخته رخ داد.");
    } finally {
      setIsLoading(false);
    }
  }, [id]);

  useEffect(() => {
    fetchShop();
  }, [fetchShop]);

  // --- Handlers ---

  // تابع برای مدیریت تایید/رد مدارک
  const handleConfirmation = (field: keyof Shop, value: boolean, title: string) => {
    if (!shop) return;

    // تابع اصلی که API را فراخوانی می‌کند و در onConfirm مودال قرار می‌گیرد
    const executeConfirmation = async () => {
      setActionLoading(true);
      try {
        const key = (field.charAt(0).toUpperCase() + field.slice(1)) as keyof any;
        const updatedShop = await confirmShopRegistration({
          ShopId: shop.id,
          [key]: value,
        });
        setShop(updatedShop); // به‌روزرسانی state با پاسخ جدید
      } catch (err) {
        console.error(err);
        alert("خطا در به‌روزرسانی وضعیت.");
      } finally {
        setActionLoading(false);
        setModalState({ isOpen: false, title: '', message: '', onConfirm: () => {} }); // بستن و ریست کردن مودال
      }
    };
    
    // تنظیم و باز کردن مودال
    setModalState({
      isOpen: true,
      title: `تایید عملیات`,
      message: `آیا از ${value ? 'تایید' : 'رد'} کردن "${title}" مطمئن هستید؟`,
      onConfirm: executeConfirmation,
    });
  };
  
  // تابع برای تایید نهایی فروشگاه
  const handleFinalConfirm = () => {
    if (!shop) return;
    
    // تابع اصلی که API را فراخوانی می‌کند
    const executeFinalConfirm = async () => {
      setActionLoading(true);
      try {
        const updatedShop = await confirmFinalShopStatus(shop.id);
        setShop(updatedShop);
      } catch (err) {
        console.error(err);
        alert("خطا در تایید نهایی فروشگاه.");
      } finally {
        setActionLoading(false);
        setModalState({ isOpen: false, title: '', message: '', onConfirm: () => {} });
      }
    };
    
    // تنظیم و باز کردن مودال
    setModalState({
        isOpen: true,
        title: "تایید نهایی فروشگاه",
        message: `آیا از تایید نهایی فروشگاه "${shop.shopTitle}" مطمئن هستید؟`,
        onConfirm: executeFinalConfirm,
    });
  };

  const renderContent = () => {
    if (isLoading) return <p>در حال بارگذاری اطلاعات فروشگاه...</p>;
    if (error) return <p style={{ color: 'red' }}>خطا: {error}</p>;
    if (!shop) return <p>اطلاعات فروشگاه یافت نشد.</p>;

    const isConfirmDisabled = shop.registrationStatus === 'Confirmed';
    const allDocumentsConfirmed = 
        shop.isLogoConfirmed &&
        shop.isShopTitleConfirmed &&
        shop.isNationalCardConfirmed &&
        shop.isBusinessLicenseConfirmed &&
        shop.isTellConfirmed;

    return (
      <>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
          <EntityHeader
              title={shop.shopTitle}
              avatarSrc={shop.logo ? `https://api.yadakchi.com${shop.logo}` : undefined}
          >
              <Badge 
                  label={translateStatus(shop.registrationStatus)} 
                  variant={shop.registrationStatus === 'Confirmed' ? 'success' : 'warning'}
              />
          </EntityHeader>

          <ActionToolbar onBackClick={() => router.back()}>
            {allDocumentsConfirmed && !isConfirmDisabled && (
              <Button 
                variant="primary" 
                onClick={handleFinalConfirm} 
                isLoading={actionLoading && modalState.title.includes("نهایی")}
              >
                <CheckBadgeIcon style={{width: '1.2rem', height: '1.2rem'}}/>
                <span>تایید نهایی فروشگاه</span>
              </Button>
            )}
             {isConfirmDisabled && (
                <Label text="فروشگاه تایید شده است" variant="secondary" />
             )}
          </ActionToolbar>

          <InfoCard 
            title="اطلاعات فروشگاه"
            items={[
              { label: "نام فروشنده", value: `${shop.name} ${shop.lastname}` },
              { label: "تلفن", value: shop.tell || "-" },
              { label: "شماره همراه", value: shop.phoneNumber || "-" },
            ]}
          />
          
          <InfoCard title="بررسی و تایید مدارک">
            <div style={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}>
              <ApprovalCard 
                title="لوگو"
                isConfirmed={shop.isLogoConfirmed}
                documentUrl={shop.logo}
                 rejectionDescription={shop.logoConfirmationDescription} 
                onApprove={() => handleConfirmation('isLogoConfirmed', true, "لوگو")}
                onReject={() => handleConfirmation('isLogoConfirmed', false, "لوگو")}
                isLoading={actionLoading}
              />
              <ApprovalCard 
                title="عنوان فروشگاه"
                isConfirmed={shop.isShopTitleConfirmed}
                onApprove={() => handleConfirmation('isShopTitleConfirmed', true, "عنوان فروشگاه")}
                onReject={() => handleConfirmation('isShopTitleConfirmed', false, "عنوان فروشگاه")}
                isLoading={actionLoading}
              />
              <ApprovalCard 
                title="کارت ملی"
                isConfirmed={shop.isNationalCardConfirmed}
                documentUrl={shop.nationalCard}
                onApprove={() => handleConfirmation('isNationalCardConfirmed', true, "کارت ملی")}
                onReject={() => handleConfirmation('isNationalCardConfirmed', false, "کارت ملی")}
                isLoading={actionLoading}
              />
               <ApprovalCard 
                title="جواز کسب"
                isConfirmed={shop.isBusinessLicenseConfirmed}
                documentUrl={shop.businessLicense}
                onApprove={() => handleConfirmation('isBusinessLicenseConfirmed', true, "جواز کسب")}
                onReject={() => handleConfirmation('isBusinessLicenseConfirmed', false, "جواز کسب")}
                isLoading={actionLoading}
              />
              <ApprovalCard 
                title="تلفن ثابت"
                isConfirmed={shop.isTellConfirmed}
                onApprove={() => handleConfirmation('isTellConfirmed', true, "تلفن ثابت")}
                onReject={() => handleConfirmation('isTellConfirmed', false, "تلفن ثابت")}
                isLoading={actionLoading}
              />
            </div>
          </InfoCard>
        </div>

        <Modal
          isOpen={modalState.isOpen}
          onClose={() => setModalState({ ...modalState, isOpen: false })}
          title={modalState.title}
          footer={
            <div style={{display: 'flex', gap: '0.75rem'}}>
              <Button variant="secondary" onClick={() => setModalState({ ...modalState, isOpen: false })}>
                انصراف
              </Button>
              <Button variant="primary" onClick={modalState.onConfirm} isLoading={actionLoading}>
                بله، تایید می‌کنم
              </Button>
            </div>
          }
        >
          <p>{modalState.message}</p>
        </Modal>
      </>
    );
  };

  return (
    <YadakchiAdminLayout currentPath={pathname} renderLink={renderLink}>
      {renderContent()}
    </YadakchiAdminLayout>
  );
}